@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap');
body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif, 'Noto Sans JP';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 auto;
  background: linear-gradient(to top,gray, 90%, #0066FF);
  background-attachment:fixed;
  color: white;
}

.Generate{

}

.header{
  color: aliceblue;
  text-align: center;
  margin: 0;
  padding: 10px;
  font-size: 30px;
  font-family: 'Josefin Sans', sans-serif;
}

.formLabel{
  display: flex;
  width: 300px;
  margin: 0 auto;
  color: white;
  font-size: 14px;
}

.formItem{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px auto 50px auto;
}


.formItem input{
  border: none;
  height: 30px;
  padding: 10px;
  width: 300px;
  font-size: 20px;

  border-radius: 30px;
}

.formItem input:focus{
  outline: none;
  background-color: azure;
  opacity: 0.5;
}

.formItem button{
  width: 200px;
  margin: 60px auto;
  font-size: 20px;
  padding: 20px;
  background-color:rgba(0, 102, 255,0.5);
  border-radius: 20px;
  cursor: pointer;
  color: white;
  border: none;
}

.loading{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.footer{
  position: fixed;
  bottom: 0;
  height: 40px;
  background-color: white;
  width: 100%;
}

.playlist{
  width: 85%;
  margin: 0 auto 100px auto;
}

.playlist a{
  text-decoration: none;
  color: white;
}

.playlist h2{
  color: white;
}

.playlist1, .playlist2, .playlist3 {
  display: flex;
  gap: 20px;
  cursor: pointer;
  overflow: scroll;
}

.tune{
  padding: 15px;
  background-color:rgba(0,0,0,0.3);
  border-radius: 5px;
}

.tune:hover{
  opacity: 0.5;
}

.tuneTitle{
  font-size: 20px;
  font-weight: bold;
  margin-bottom: -10px;
}

.playlist img{
  width: 200px;
  height: 200px;
}

.modal{
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  transform: translate(-50%, -50%);
  width:400px;
  height: 400px;
  border: none;
  background-color: white;
  text-align: center;
  border-radius: 5px;
  outline: none;
  color:black;
}

.modal img{
  width: 300px;
  margin-top:10px;
}

.rhap_container{
  position: absolute;
  bottom:0;
}

.artistHome{

}

.artistHome header{
  height: 400px;
  width: 100vw;
}

.artistHomeSubheader{
  font-family: 'Josefin Sans', sans-serif;
  margin-left: 50px;
  font-size: 20px;
}

.artistHeader{
  font-family: 'Josefin Sans', sans-serif;
  margin-top: 150px;
  margin-left: 50px;
}

.artistHeader h1{
  font-size: 80px;
}

.artistHeader p{
  font-size: 40px;
  margin-bottom: -50px;
}

.artistHeader button{
  border: 1px solid #0066FF;
  background-color: white;
  color: #0066FF;
  font-weight: bold;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  margin-top: -20px;
  width: 100px;
}



.artistHomeHeaderImage img{
  position: absolute;
  width: 100vw;
  top:0;
  z-index: -100;
  opacity: 1;
  height: 400px;
  object-fit:cover;
}
.artistHomeTune{
  display: flex;
  gap: 30px;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  padding: 50px;
  background-color: rgba(0,0,0,0.3);
}
.artistHomeTune img{
  width: 150px;
  height: 150px;
}
.musicTitle{
  width:200px;
}
.playButton{
  cursor: pointer;
  font-size: 50px;
}

.playButton:hover{
  opacity: 0.3;
}

@media screen and (max-width:767px) {
  .playlist img{
    width: 100px;
    height: 100px;
  }
  .tuneTitle{
    font-size: 14px;
  }
  .artistHeader{
    margin-top: 200px;
    margin-left: 20px;
  }
  .artistHeader h1{
    font-size: 50px;
  }
  .artistHeader p{
    font-size: 30px;
    margin-bottom: -20px;
  }
  .artistHeader button{
    padding: 10px;
    margin: 5px;
    border-radius: 5px;
    margin-top: -20px;
    width: 70px;
    font-size: 12px;
  }
  .artistHomeSubheader{
    margin-top: -150px;
  }
  .artistHomeTune img{
    width: 100px;
    height: 100px;
  }
}
